.main-promo {
  position: relative;
  min-height: 389px;
  background-image: url("../../img/jpg/back-mobile.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-color: $primaryColor;
  text-align: center;
}

.main-promo__text {
  margin: 0 auto;
  padding-top: 90px;
}

.main-promo::after {
  position: absolute;
  bottom: 0;
  display: block;
  width: 100%;
  height: 57px;
  background-image: url("../../img/intro-triangle-big.svg");
  background-repeat: no-repeat;
  background-position: center;
  content: "";
}
